import Vue from 'vue';
import Component from 'vue-class-component';

@Component
export class TagHelper extends Vue{
    createTag() {
        const name = window.prompt('');
        if (name !== null) {
            const tagName = name.trim();
            if (tagName === '') {
                return window.alert('');
            }else {
                this.$store.commit('createTag', tagName);
                if (this.$store.state.createTagErr){
                    return window.alert('');
                }else{
                    window.alert('')
                }
            }
        }
    }
}